require("./event.scss");
require("../../var/copyedText");
$(function () {
    $('[data-type="part-nav"] li').click(function () {
        console.log('clicked');
        if ($(this).hasClass('active')) {
            return;
        }
        var $active = $('[data-type="part-nav"] li.active');
        $active.removeClass('active');
        var oldActivePartName = $active.attr('data-part');
        $(this).addClass('active');
        var partName = $(this).attr('data-part');
        $("[data-type=\"part-part\"][data-part=\"" + oldActivePartName + "\"]").slideUp();
        $("[data-type=\"part-part\"][data-part=\"" + partName + "\"]").slideDown();
    });
});
